.inputGroup {
  display: grid;
}

.loginInput {
  min-width: 15px;
  padding: 10px;
  border: 1px solid var(--light-grey-color);
  font-size: 24px;
  height: 40px;
}

.loginInput:focus {
  outline: none;
  border: 2px solid black;
}

.error {
  color: var(--danger-color);
  font-size: 22px;
}
